import React, { Component } from 'react';
import { Table, CheckPicker, Button, Modal } from 'rsuite';

const { Column, HeaderCell, Cell } = Table;

class Elcap extends Component {

  constructor(props) {
    super(props);
    this.keys = ['manufacture', 'series', 'cap', 'volt', 'd', 'h'];
    this.emptyOptions = this.getEmptyOptions(this.keys);
    this.tableHeight = window.innerHeight - 100;

    this.state = {
      loading: true,
      data: [],
      table: [],
      options: {},
      selectedOptions: {},
      show: false,
    };
    
    this.selectOption = this.selectOption.bind(this);
    this.clear = this.clear.bind(this);
    this.open = this.open.bind(this);
    this.close = this.close.bind(this);
  }

  componentDidMount() {
    fetch('./data/data.json')
      .then(response => response.json())
      .then(data =>
        this.setState({
          loading: false,
          data,
          table: data,
          options: this.getFullOptions(data),
          selectedOptions: {},
        })
      );
  }

  getEmptyOptions(data) {
    return data.reduce((acc, key) => {
      acc[key] = [];
      return acc;
    }, {});
  }

  getFullData(data) {

  }

  getFullOptions(data) {
    return this.keys.reduce((set, key) => {
      let arr = [...new Set(data.map(i => i[key]))];
      if (key === 'volt') arr = arr.sort((a, b) => {
        a = a.replace('V', '');
        b = b.replace('V', '');
        return a - b;
      });
      const options = arr.map(i => {
        let data = {};
        data.value = i;
        data.label = i;
        return data;
      })
      set[key] = options;
      return set
    }, {});
  }

  selectOption(arr, key) {
    const selectedOptions = { ...this.state.selectedOptions };
    selectedOptions[key] = [...arr];
    let table = [...this.state.data];

    Object.keys(selectedOptions).forEach(key => {
      const arr = selectedOptions[key];
      if (arr.length !== 0) {
        table = table.filter(item => arr.some(i => i === item[key]));
      }
    })

    this.setState({ selectedOptions, table });
  }

  clear() {
    this.setState({
      selectedOptions: this.emptyOptions,
      table: [...this.state.data],
    })
  }

  open() {
    this.setState({ show: true })
  }

  close() {
    this.setState({ show: false })
  }

  render() {
    return (
      <div style={{ padding: 20 }}>

        <div style={{ height: 50 }}>

          {this.keys.map(key => (
            <CheckPicker
              size="sm"
              key={key}
              searchable={false}
              placeholder={key}
              data={this.state.options[key]}
              onSelect={value => this.selectOption(value, key)}
              onClean={() => this.selectOption([], key)}
              value={this.state.selectedOptions[key]}
              style={{ minWidth: 120, marginRight: 20 }}
            />
          ))}

          <Button style={{ marginRight: 10 }} size="sm" appearance="primary" onClick={this.clear}>Очистить</Button>
          <Button size="sm" appearance="primary" onClick={this.open}>Как заказать</Button>
          {/* <Button size="sm" appearance="link" onClick={this.clear}>Скачать табличку для заказа</Button> */}

          {/* <Button onClick={() => console.log(this.state)}>State</Button> */}

        </div>

        <Table
          virtualized
          affixHeader
          bordered
          loading={this.state.loading}
          height={this.tableHeight}
          data={this.state.table}
        >

          <Column width={100} align="center">
            <HeaderCell>id</HeaderCell>
            <Cell dataKey="id" />
          </Column>

          <Column width={150} align="center">
            <HeaderCell>Производитель</HeaderCell>
            <Cell dataKey="manufacture" />
          </Column>

          <Column width={100} align="center" >
            <HeaderCell>Серия</HeaderCell>
            <Cell dataKey="series" />
          </Column>

          <Column width={100} align="center" >
            <HeaderCell>Емкость, uF</HeaderCell>
            <Cell dataKey="cap" />
          </Column>

          <Column width={100} align="center" >
            <HeaderCell>Напряжение, V</HeaderCell>
            <Cell dataKey="volt" />
          </Column>

          <Column width={100} align="center" >
            <HeaderCell>Diameter</HeaderCell>
            <Cell dataKey="d" />
          </Column>

          <Column width={100} align="center" >
            <HeaderCell>Height</HeaderCell>
            <Cell dataKey="h" />
          </Column>

          <Column width={120} align="center" >
            <HeaderCell>Кратность заказа</HeaderCell>
            <Cell dataKey="min" />
          </Column>

          <Column width={100} align="center" >
            <HeaderCell>Price, RUB</HeaderCell>
            <Cell dataKey="sell" />
          </Column>

          <Column flexGrow={1}>
            <HeaderCell>Full Name</HeaderCell>
            <Cell dataKey="fullData" />
          </Column>

        </Table>

        {/* <div style={{lineHeight: "50px"}}> */}
        {/* Total: {this.state.table.length} */}
        {/* <Pagination
            activePage={this.state.page}
            total={this.state.table.length}
          /> */}
        {/* </div> */}



        <Modal show={this.state.show} onHide={this.close}>
          <Modal.Header>
            <Modal.Title>Как заказать</Modal.Title>
          </Modal.Header>
          <Modal.Body>
            <h6 style={{ marginBottom: 5 }}>Заказ</h6>
            <p>Так как модуль корзины ещё не написан, оформить заказ пока можно таким способом:</p>
            <p className="">1. Копируем <a href="https://docs.google.com/spreadsheets/d/1PGzXwmvqvHan0lavNT32fbpCKWAcyEQOvY4LlVadejw/edit?usp=sharing" target="_blank" rel="noopener noreferrer">Форму заказа</a> на комп или гугл докс:</p>
            <p className="">2. Копируем в таблицу наименование (колонка Full Name), количество</p>
            <p className="">3. Заполняем ФИО, адрес, телефон</p>
            <p className="">4. Отправляем табличку (эксель или ссылку на Гуглдокс на <a href="mailto:diyaudioby@gmail.com">diyaudioby@gmail.com</a></p>
            <hr />
            <h6 style={{ marginBottom: 5 }}>Оплата</h6>
            <p>Оплатить можно любым удобным способом - Перевод на карту, Яндекс.Деньги, Webmoney</p>
            <hr />
            <h6 style={{ marginBottom: 5 }}>Доставка</h6>
            <p className="">Территориально нахожусь в г. Минск, Беларусь</p>
            <p>Доставка - Почта, СДЕК, в любую точку мира.</p>
            <p>По РФ и странам СНГ рекомендую выбирать СДЕК, так как ёмкости тяжёлые, почта может и повредить упаковку или банки. Были случаи.</p>
            <hr />
            <h6 style={{ marginBottom: 5 }}>Упаковка</h6>
            <p>Пакую заказ в плотные картонные коробки с двойными стенками, плёнки пупырчатой не жалею :)</p>

          </Modal.Body>
          <Modal.Footer>
            <Button onClick={this.close} appearance="primary">
              Ok
            </Button>
          </Modal.Footer>
        </Modal>
      </div>
    )
  }

}

export default Elcap;
